.switch {
  position: relative;
  margin-top: 4px;
  margin-left: 10px;
}

.switch label {
  width: 45px;
  height: 23px;
  background-color: #999;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50px;
}

.switch input[type="checkbox"] {
  visibility: hidden;
}

.switch label:after {
  content: '';
  width: 21px;
  height: 21px;
  border-radius: 50px;
  position: absolute;
  top: 1px;
  left: 1px;
  transition: .3s;
  background-color: white;
}

.switch .look:checked + label:after {
  left: 23px;
}

.switch .look:checked + label {
  background-color: #3AF219;
}

